
import { defineComponent, toRefs } from "vue";
import { useRouter } from "vue-router";
import { ElNotification } from "element-plus";
import { formatImageUrl } from "@/utils/image";

const Component = defineComponent({
  name: "FabricCard",
  props: {
    data: {
      type: Object,
      default: () => ({
        id: "id",
        main_media: {
          file: ""
        },
        name: "",
        real_composition: "",
        favorite: false
      })
    },
    hasStatus: {
      type: Boolean,
      default: false
    },
    hasButtons: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const router = useRouter();
    const { data, hasButtons } = toRefs(props);

    const onImageClick = async () => {
      await router.push({
        name: "FabricDetail",
        params: { id: data.value.id }
      });
    };

    const onFavoriteClick = () => {
      if (!hasButtons) return;
      ElNotification({
        title: "Success",
        type: "success",
        message: `Add ${data.value.name} to Favorite successful.`
      });
    };

    const onAddClick = () => {
      if (!hasButtons) return;
      ElNotification({
        title: "Success",
        type: "success",
        message: `Add`
      });
    };

    return {
      onImageClick,
      onFavoriteClick,
      onAddClick,
      formatImageUrl
    };
  }
});

export default Component;
